.footer {
	display: flex;
	justify-content: center;
	padding-top: 11rem;
	padding-bottom: 2rem;
	background-color: rgba($blue, .05);

	&-container {
		width: grid(10);
	}

	&-post {
		margin-bottom: 15rem;
	}

	&-title {
		width: grid(6);
		color: $blue;
		font-size: 5rem;
	}

	&-text {
		width: grid(4);
		margin-top: 5rem;
		margin-bottom: 14rem;
		font-size: 4rem;
		line-height: 1.5;
		opacity: .5;
	}

	&-separator {
		width: 100%;
		height: .2rem;
		background-color: rgba($black, .5);
	}

	&-lists {
		display: flex;
		margin-top: 6rem;
	}

	&-column {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin-right: $gutter * 2;

		&:last-child {
			margin-right: 0;
		}
	}

	&-section {
		margin-bottom: 3.5rem;
		font-size: 2.5rem;
		opacity: .75;
	}

	&-link {
		margin-bottom: 3rem;
		color: rgba($black, .5);
		font-size: 2rem;
		transition: color .125s ease-in-out;

		&:last-child {
			margin-bottom: 0;
		}

		&:hover {
			color: $green;
		}
	}


	&-copyright {
		padding: 1.5rem;
		color: $white;
		font-size: 3rem;
		text-align: center;
		background-color: $blue;
		border-radius: .5rem;
	}
}