/*
 * http://meyerweb.com/eric/tools/css/reset/
 * v2.0 | 20110126
 * License: none (public domain)
 */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

body {
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote::before,
q::before,
blockquote::after,
q::after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

*,
*::before,
*::after,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

input,
button,
textarea,
select {
	color: inherit;
	font-size: inherit;
	font-style: inherit;
	font-family: inherit;
	-webkit-border-radius: 0;
	border-radius: 0;
	-webkit-padding-start: 0;
	align-items: flex-start;
	text-index: 0;
	border: none;
	outline: none;
	background: none;
	padding: 0;
	margin: 0;
	width: auto;
	height: auto;
	line-height: 1em;
}

input[type=text],
input[type=reset],
input[type=password],
input[type=search],
input[type=email],
input[type=tel],
input[type=url],
input[type=time],
input[type=week],
input[type=month],
input[type=date],
input[type=datetime],
input[type=datetime-local],
input[type=number],
input[type=submit],
input[type=color],
input[type=file],
button,
textarea,
select {
	height: 1em;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

input[type=color] {
	width: 1em;
}

input::-ms-clear {
	display: none;
}

details,
summary {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

body {
	-webkit-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	text-size-adjust: 100%;
}

mark {
	background: none;
}

hr {
	height: 1px;
	margin: 0;
	padding: 0;
}

u {
	text-decoration: none;
}

table th {
	text-align: left;
}

a {
	color: inherit;
	outline: none;
	text-decoration: none;
}